.tabs__nav {
  align-items: stretch;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0; }
  .tabs__nav li {
    position: relative;
    align-items: center;
    cursor: pointer;
    display: inline-flex;
    margin: 0 16px;
    padding: 0 0 16px; }
    .tabs__nav li:before {
      content: '';
      left: 0;
      right: 0;
      bottom: 0;
      height: 2px;
      background: #962DA1;
      position: absolute;
      display: block;
      transform-origin: 0 0;
      transition: transform 400ms cubic-bezier(0.23, 1, 0.32, 1);
      transform: scaleX(0); }
    .tabs__nav li.is-active:before, .tabs__nav li:hover:before {
      transform: scaleX(1); }
    .tabs__nav li > span,
    .tabs__nav li > strong {
      margin-left: 8px;
      margin-right: 8px; }
    .tabs__nav li:first-child {
      margin-left: 0; }
      .tabs__nav li:first-child > span,
      .tabs__nav li:first-child > strong {
        margin-left: 0; }
    .tabs__nav li > strong {
      margin-right: 0; }

