/* =========================================
Button Component
========================================= */

.button {
  display: inline-flex;
  vertical-align: middle;
  justify-content: center;
  align-content: center;
  font: inherit;
  text-align: center;
  margin: 0;
  cursor: pointer;
  font-weight: $t-font-weight-bold;
  border-radius: $b-radius;
  color: $ca-button-color;

  &:hover {
  }
}

.button--primary {
  padding: $d-base $d-duo;
  background: $ca-button-primary-background;
  color: $ca-button-primary-color;

  &:hover {
    background: $ca-button-primary-background;
    color: $ca-button-primary-color;
  }
}

.button--secondary {
  padding: $d-base $d-duo;
  background: $ca-button-secondary-background;
  color: $ca-button-secondary-color;

  &:hover {
    background: $ca-button-secondary-background;
    color: $ca-button-secondary-color;
  }
}

.button--circle {
  border-radius: 50%;
  height: $d-quin;
  width: $d-quin;
  border: $b-default;
  padding: 0;

  .icon {
    align-self: center;
    height: $d-trio;
    width: $d-trio;
  }
}


.button--large {
  padding: $d-duo $d-quad;
  font-size: $t-font-size-button-large;
}
